.profile__avatar-button {
  height: 120px;
  margin-right: 30px;
  position: relative;
  width: 120px;
  cursor: pointer;
}

.profile__avatar-button:hover:after {
  opacity: 1;
}

.profile__avatar-button::after {
  background-color: rgba(0, 0, 0, 0.8);
  background-image: url(../../../images/edit_avatar.png);
  background-position: 50%;
  background-repeat: no-repeat;
  bottom: 0;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.3s;
  width: 100%;
}
