.popup__button {
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 21.78px;
  color: #FFFFFF;
  background-color: #000000;
  width: 358px;
  text-align: center;
  border-radius: 2px;
  transition: 0.3s;
  margin-top: 42px;
  padding: 14px 0 14px;
  margin-bottom: 34px;
}

.popup__button:hover {
  opacity: 0.8;
  transition: 0.3s;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .popup__button {
    width: 238px;
    font-size: 14px;
    line-height: 16.94px;
    margin-bottom: 25px;
  }
}
